export default {
	data() {
		return {
			List:[],
			ListLen:0,
		}
	},
	onLoad:function(){
		var _self = this
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/releaseQuery",
				data:{id:"0",optionId :uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					_self.List = res.data
					_self.ListLen = res.data.length
					console.log(_self.List.length)
					console.log(res.data)
				}
			})
		}, 
		goDetail:function(id,mark,branch){
			var _self = this
			if(branch ==0){
					uni.navigateTo({
						url:"../../infoPage/infoDetail/infoDetail?id="+id+'&mark='+mark
					})
				
			}else{
				uni.navigateTo({
				url:"../../pageChildren/productDetail/productDetail?id="+id+'&mark='+mark+'&branch='+branch
				})
			}
		}
	},
		filters:{
		getPic:function(res){
			return res.split(',')[0]
		}
	}
}
